/* -----------------초기화 */
@font-face {
    font-family: "Lobster";
    font-weight: 400;
    src: url(font/Lobster-Regular.ttf) format("truetype");
}
@font-face {
    font-family: "SongMyung";
    font-weight: 400;
    src: url(font/SongMyung-Regular.ttf) format("truetype");
}
@font-face {
    font-family: "AbrilFatface";
    font-weight: 400;
    src: url(font/AbrilFatface-Regular.ttf) format("truetype");
}
body {
    background: linear-gradient(to bottom, #F7C5CC, #FCEDDA);
    background-repeat: no-repeat;
    background-size: cover;
    text-align: center;
    margin: 0;
    padding: 0;
    width: 960px;
    font-family: "AbrilFatface","SongMyung";
}
html {
    margin: 0 auto;
    padding: 0;
    width: 960px;
    background-color: black;
}
li, a{
    
    list-style: none;
    text-decoration: none;
}

/* ------------------------------메뉴영역 */
.homeName a img{
    width: 960px;
    margin-bottom: 30px;
}
.SSS{
    padding: 0;
    height: 90px;
    margin-top: 50px;
    margin-bottom: 50px;
    background-color: rgb(176, 195, 255);
}
.SSS li {
    display: inline;
    height: 90px;
    font-size: 20px;
}
.SSS li a {
    display: inline-block;
    height: 90px;
    padding: 0 50px;
    color: rgb(57, 104, 88);
    line-height: 90px;
    font-weight: bold;
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
}
.SSS li a:hover {
    background-color: #0038a1;
    color: #fff;
}
/* ------------------------------히스토리 */
.thisIsHistory {
    font-family: '맑은 고딕';
}
.thisIsHistory div {
    overflow: hidden;
    width: 960px;
    height: 500px;
}
.historyimg {
    width: 960px;
}
.thisIsHistory p {
    font-weight: bold;
    font-size: 16px;
}

/* -----------------------------------메뉴 영역*/
.thisMenu>ul>div {
    width: 350px;
    height: 400px;
    margin: 10px 5px;
    display: inline-block;
    background-size: cover;
    position: relative;
}
.thisMenu div li{
    font-size: 16px;
    font-weight: bold;
}
.thisMenu .menuName1 {
    background-image: url('images/Lemon.jpg');
}
.thisMenu .menuName2 {
    background-image: url('images/Cherry.jpg');
}
.thisMenu .menuName3 {
    background-image: url('images/Blueberry.jpg');
}
.thisMenu .menuName4 {
    background-image: url('images/Strawberry.jpg');
}
.thisMenu .menuName5 {
    background-image: url('images/cookie.jpg');
}
.thisMenu .menuName6 {
    background-image: url('images/greenTea.jpg');
}
.thisMenu ul div li{
    background-color: rgb(51, 51, 51);
}
.thisMenu ul .menuName1 li {
    color: rgb(255, 255, 0);
}
.thisMenu ul .menuName2 li {
    color: rgb(255, 0, 123);
}
.thisMenu ul .menuName3 li {
    color: rgb(76, 0, 255);
}
.thisMenu ul .menuName4 li {
    color: rgb(255, 0, 0);
}
.thisMenu ul .menuName5 li {
    color: rgb(56, 40, 40);
}
.thisMenu ul .menuName6 li {
    color: rgb(59, 125, 63);
}
.thisMenu ul div li {
    padding: 5px;
    text-shadow: 1px 1px 1px #fff, -1px -1px 1px #fff;
}
.thisMenu a {
    background-color: rgb(170, 255, 227);
    color: #ff3c00;
    border-radius: 10%;
    padding: 3px;
}
/* 메뉴 설명 영역 */

.thisMenu>ul>div>div {
    position: absolute;
    width: 350px;
    top: 310px;
    background-color: rgba(0, 0, 0, 0.5);
    color: #fff;
}
#prod_pic {
    overflow: hidden;
    height: 500px;
}
#prod_pic img{
    width: 500px;
}
/* --------------------------------사진영역 */

.introPhoto > .introimg {
    width: 960px;
}





/* 애니메이션 이미지 영역 */
.changeMacaroon li, .changeMacaroon2 li{
    display: inline;
}
.changeMacaroon li img, .changeMacaroon2 li img{
    width: 150px;
}
header {
    width: 100%;
    height: 10%;
}
@keyframes rotate {
    from {
        transform: rotate(0deg);
    }
    to {
        transform: rotate(360deg);
    }
}
.changeMacaroon ul li:nth-child(1) img {
    animation-name: rotate;
    animation-duration: 6s;
    animation-iteration-count: infinite;
    animation-timing-function: linear;
    animation-direction: reverse;
}
.changeMacaroon ul li:nth-child(2) img {
    animation-name: rotate;
    animation-duration: 6s;
    animation-iteration-count: infinite;
    animation-timing-function: linear;
    animation-direction: reverse;
    animation-delay: 1s;
}
